.wrap{
    display: flex;
    gap: 7px;
    max-height: 28px;
}
.activeDraw{
    border-radius: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 25px;
    min-width: 25px;
    max-height: 28px;
    padding: 2px;
    background-color: #000000;
}
.activeDraw img{
    filter: invert(48%) sepia(18%) saturate(733%) hue-rotate(175deg) brightness(90%) contrast(92%);
    width: 25px;
    height: 25px;
}
.disabledDraw{
    border-radius: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 25px;
    min-width: 25px;
    max-height: 28px;
    padding: 2px;
    cursor: pointer;
    border-radius: 5px;
}
.disabledDraw img{
    width: 25px;
    height: 25px;
    filter: invert(100%) sepia(7%) saturate(1870%) hue-rotate(187deg) brightness(107%) contrast(108%);
}
.disabledDraw:hover{
    background-color: #000000;
}