.wrap{
    padding: 5px 10px 10px 5px;
    align-items: center;
    border-bottom:2px solid  #2a2e39;
    background-color: #131722;
    white-space: nowrap;
    width: 100%;
    height: 79px;
    flex-basis: auto;
    gap: 20px;
}
@media(max-width:790px){
    .wrap{
        overflow-x: scroll;
    }
}