.wrap{
    display: flex;
    gap: 7px;
    align-items: center;
    position: relative;
}
.active img{
    width: 26px;
    height: 26px;
    cursor: pointer;
    display: block;
    filter: invert(48%) sepia(18%) saturate(733%) hue-rotate(175deg) brightness(90%) contrast(92%);
}
.disabled img{
    width: 26px;
    cursor: pointer;
    height: 26px;
    display: block;
    filter: invert(98%) sepia(1%) saturate(5%) hue-rotate(18deg) brightness(102%) contrast(101%);
}
.active{
    padding: 4px;
    background-color: #000000;
}
.disabled{
    padding: 4px;
    border-radius: 6px;  
}
.disabled:hover{
    background-color: #000000;
}
