.modal{
    position: absolute;
    top:33px;
    width: 230px;
    background-color: #1b1b1b;
    z-index: 10;
    /* border: 1px solid #474747; */
}
.wrap{
    padding: 5px 10px;
    display: flex;
    flex-direction: column;
    height: 100%;
}
.group{
    display: flex;
    align-items: center;
    gap: 10px;
    color: #fff;
    padding: 15px 5px;
    font-size: 15px;
    border-bottom: 1px solid #474747;
    
}
.modes{
    display: flex;
    align-items: center;
    gap: 5px;
    flex-wrap: wrap;
}
.wrap div:nth-child(4){
    border: none;
}
.mode{
    cursor: pointer;
    filter: invert(98%) sepia(1%) saturate(5%) hue-rotate(18deg) brightness(102%) contrast(101%);
}
.activeMode{
    filter: invert(48%) sepia(18%) saturate(733%) hue-rotate(175deg) brightness(90%) contrast(92%);
}